import Link from 'next/link'
import Nav from './Nav'
import styled from 'styled-components'

const Logo = styled.h1`
  font-size: 4rem;
  margin-left: 2rem;
  position: relative;
  z-index: 2;
`

const Header = () => (
  <div>
    <div className="bar">
      <Logo>
        <Link href="/">
          <a>Kindred</a>
        </Link>
      </Logo>
      <Nav />
    </div>
    <div className="sub-bar">
      <p>search</p>
    </div>
  </div>
)

export default Header
